.User {

}

.User-Info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-bottom: 36px;
}

.User-Content { 
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;

}

.User-image {
    grid-area: Image;
    -webkit-transition: -webkit-filter ease 0.3s;
    transition: -webkit-filter ease 0.3s;
    -o-transition: filter ease 0.3s;
    transition: filter ease 0.3s;
    transition: filter ease 0.3s, -webkit-filter ease 0.3s;
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
    max-height: 400px;
    max-width: 400px;
    border-radius: 50%;
    border: 2px solid white;
    -webkit-transform: scale(0.2);
        -ms-transform: scale(0.2);
            transform: scale(0.2);
    opacity: 0;
    -webkit-transition: all 0.2s cubic-bezier(0.4, 0.0, 0.2, 1);
    -o-transition: all 0.2s cubic-bezier(0.4, 0.0, 0.2, 1);
    transition: all 0.2s cubic-bezier(0.4, 0.0, 0.2, 1);
}

@media (max-width: 500px) {
    .User-image {
        max-height: 300px;
        max-width: 300px;
    }
  }


.User-image.PopIn {
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}

.User-image > img {
    height: auto;
    width: auto;
    max-height: 500px;
}

.User-Wishes {

}

.UserWishCard {
    border-bottom: 1px solid #BDBDBD;
}

.UserWishCard:last-of-type {
    border-bottom: none;
}


